@import url(https://fonts.googleapis.com/css2?family=Source+Sans+Pro:wght@200;300;400;600;700;900&display=swap);
:root {
  --clr-primary: #5870b5;
  --clr-secondary: #3e4e80;
  --clr-dark-grey: #666;
  --clr-light-grey: #a1a1a1;
  --clr-extralight-grey: #e9e9e9;
  --fnt-size-sm: 0.875rem;
  --fnt-size-md: 1rem;
  --fnt-size-lg: 1.125rem;
  --fnt-size-xl: 1.25rem;
  --fnt-size-2xl: 1.5rem;
  --fnt-size-3xl: 2rem;
  --fnt-size-4xl: 2.5rem;
  --screen-padding: 1.5rem;
}

* {
  margin: 0;
  font-family: "Source Sans Pro", sans-serif;
}

.app {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: 100%;
}

:root {
  --header-height: 5rem;
}

.header {
  display: flex;
  align-items: center;
  justify-content: space-around;
  grid-column-gap: 1rem;
  -webkit-column-gap: 1rem;
          column-gap: 1rem;
  height: 5rem;
  height: var(--header-height);
  width: 100%;
  background-color: var(--clr-primary);
  color: white;
}

.header__title {
  color: white;
  text-decoration: none;
  grid-column-gap: 0.75rem;
  -webkit-column-gap: 0.75rem;
          column-gap: 0.75rem;
  display: flex;
  justify-content: center;
  align-items: center;
  margin-left: 2rem;
}

.header__search {
  display: flex;
  align-items: center;
  grid-column-gap: 1rem;
  -webkit-column-gap: 1rem;
          column-gap: 1rem;
  padding-right: 2rem;
}

.header__heading {
  display: none;
}

@media only screen and (min-width: 700px) {
  .header__heading {
    display: flex;
  }
}

.home {
  padding: var(--screen-padding);
}

.home__heading {
  margin-bottom: 1rem;
  color: var(--clr-primary);
}

.home__subheading {
  color: var(--clr-dark-grey);
  margin-bottom: 2rem;
  max-width: 50rem;
}

.home__footer {
  display: grid;
  justify-items: center;
  justify-content: center;
  align-items: center;
  grid-template-areas: "giticon link" "copyright copyright";
  padding: 1.5rem 0;
  grid-row-gap: 1rem;
  row-gap: 1rem;
  border-top: 1px solid var(--clr-extralight-grey);
}

.home__copyright {
  grid-area: copyright;
  color: var(--clr-dark-grey);
  font-weight: 600;
}

.home__icon {
  grid-area: giticon;
  margin-right: 1rem;
}

.home__link {
  grid-area: link;
  color: var(--clr-primary);
  font-size: var(--fnt-size-lg);
  font-weight: 700;
}

@media only screen and (min-width: 700px) {
  .home {
    padding: 5rem 5rem 0 5rem;
  }

  .home__subheading {
    margin-bottom: 4rem;
  }

  .home__cards {
    display: flex;
    flex-wrap: wrap;
    grid-gap: 1rem;
    gap: 1rem;
  }

  .home__footer {
    margin-top: 5rem;
    border-bottom: 1px solid var(--clr-extralight-grey);
  }
}

@media only screen and (min-width: 900px) {
  .home__footer {
    grid-template-areas: "copyright giticon link";
  }

  .home__icon {
    margin: 0 3rem;
  }
}

.feature-card {
  display: flex;
  flex-direction: column;
  grid-row-gap: 0.5em;
  row-gap: 0.5em;
  padding: 2rem 0;
  border-top: 2px solid var(--clr-extralight-grey);
}

.feature-card__title {
  font-size: var(--fnt-size-2xl);
  font-weight: 700;
}

.feature-card__text {
  font-size: var(--fnt-size-md);
  font-weight: 600;
  color: var(--clr-dark-grey);
}

@media only screen and (min-width: 700px) {
  .feature-card {
    padding: 2rem;
    border: 4px solid var(--clr-extralight-grey);
    border-radius: 1rem;
    flex: 1 1;
  }
}

.user-page {
  display: flex;
  flex-direction: column;
  width: 100%;

  --avatar-size: 8rem;
  --avatar-size-desktop: 10rem;
  --panel-padding: 2rem;
}

.user-page__panel {
  background-color: var(--clr-extralight-grey);
  padding: var(--panel-padding);
}

.user-page__info-wrapper {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.user-page__avatar {
  height: var(--avatar-size);
  width: var(--avatar-size);
  border-radius: 1rem;
  box-shadow: 0 0 0.75rem grey;
}

.user-page__username {
  color: var(--clr-primary);
  margin-top: 0.5rem;
  font-size: var(--fnt-size-2xl);
  font-weight: 700;
}

.user-page__error-highlight {
  color: var(--clr-primary);
  font-weight: 700;
}

.user-page__error-subtext {
  font-size: var(--fnt-size-lg);
  color: var(--clr-dark-grey);
}

.user-page__spinner-wrapper {
  display: grid;
  place-items: center;
  min-height: calc(100vh - var(--header-height));
}

@media only screen and (min-width: 700px) {
  .user-page {
    flex-direction: row;
  }

  .user-page__panel {
    --doubled-padding: calc(var(--panel-padding) * 2);
    --padding-total: calc(var(--header-height) + var(--doubled-padding));

    min-height: calc(100vh - var(--padding-total));
    padding: var(--panel-padding) 4rem;
  }

  .user-page__avatar {
    height: var(--avatar-size-desktop);
    width: var(--avatar-size-desktop);
  }

  .user-page__info-wrapper {
    position: -webkit-sticky;
    position: sticky;
    top: 2rem;
  }

  .user-page__content {
    flex: 1 1;
  }
}

.user-nav__nav {
  display: flex;
  justify-content: space-around;
  border-bottom: 1px solid var(--clr-light-grey);
}

.user-nav__spinner-wrapper {
  display: grid;
  place-items: center;
}

@media only screen and (min-width: 700px) {
  .user-nav__nav {
    justify-content: flex-start;
    grid-column-gap: 3rem;
    -webkit-column-gap: 3rem;
            column-gap: 3rem;
  }

  .user-nav__nav:first-child {
    padding-left: 1.5rem;
  }
}

.nav-item {
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;
  color: var(--clr-dark-grey);
  text-decoration: none;
  padding: 1rem 0.5rem;
  transition: color 250ms ease;
}

.nav-item::before {
  content: "";
  position: absolute;
  height: 5px;
  width: 0;
  background-color: var(--clr-primary);
  bottom: -0.2rem;
  transition: width 250ms ease;
}

.nav-item--active::before {
  width: 100%;
}

.nav-item:hover,
.nav-item--active {
  color: var(--clr-primary);
}

.nav-item__text {
  margin-top: 0.25rem;
  text-transform: uppercase;
  font-size: 0.75rem;
  font-weight: 600;
}

@media only screen and (min-width: 700px) {
  .nav-item {
    flex: 1 1;
    max-width: 10rem;
  }
}
@media only screen and (min-width: 900px) {
  .nav-item {
    flex-direction: row;
    align-items: center;
    grid-column-gap: 1rem;
    -webkit-column-gap: 1rem;
            column-gap: 1rem;
    justify-content: center;
    max-width: 12rem;
  }

  .nav-item__text {
    margin-top: 0;
  }
}

@media only screen and (min-width: 700px) {
  .information {
    display: flex;
    flex-wrap: wrap;
  }
}

.info-card {
  padding: var(--screen-padding);
  display: flex;
  flex-direction: column;
}

.info-card__title {
  font-size: var(--fnt-size-2xl);
  font-weight: 700;
  padding-bottom: 0.5rem;
  margin-bottom: 0.5rem;
  border-bottom: 2px solid var(--clr-extralight-grey);
}

@media only screen and (min-width: 700px) {
  .info-card {
    flex: 1 0 33%;
    padding: 3rem;
    min-width: 20rem;
  }
}

.info-item {
  display: flex;
  grid-column-gap: 1.5rem;
  -webkit-column-gap: 1.5rem;
          column-gap: 1.5rem;
  align-items: center;
}

.info-item__text {
  font-size: var(--fnt-size-lg);
  font-weight: 600;
}

.spinner {
  --spinner-size: 4rem;

  border: 0.5rem solid var(--clr-extralight-grey);
  border-radius: 50%;
  border-top-color: var(--clr-primary);
  -webkit-animation: Spin 1000ms ease-in-out infinite;
          animation: Spin 1000ms ease-in-out infinite;
  height: var(--spinner-size);
  width: var(--spinner-size);
  display: grid;
  place-items: center;
  margin: 2rem;
}

@-webkit-keyframes Spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes Spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.spinner__dot {
  background-color: var(--clr-extralight-grey);
  border-radius: 50%;
  -webkit-animation: Pop 1500ms linear infinite;
          animation: Pop 1500ms linear infinite;
}

@-webkit-keyframes Pop {
  0% {
    height: 0%;
    width: 0%;
  }
  50% {
    height: 75%;
    width: 75%;
  }
  100% {
    height: 0%;
    width: 0%;
  }
}

@keyframes Pop {
  0% {
    height: 0%;
    width: 0%;
  }
  50% {
    height: 75%;
    width: 75%;
  }
  100% {
    height: 0%;
    width: 0%;
  }
}

.repositories {
  display: flex;
  flex-direction: column;
  padding: var(--screen-padding) 0;
  grid-gap: 2rem;
  gap: 2rem;
}

.repositories__username {
  color: var(--clr-primary);
  font-weight: 700;
}

@media only screen and (min-width: 700px) {
  .repositories {
    flex-wrap: wrap;
    flex-direction: row;
    padding: var(--screen-padding) 3rem;
  }
}

.repo-card {
  padding: 1rem 0;
  border-top: 3px solid var(--clr-extralight-grey);
  border-bottom: 3px solid var(--clr-extralight-grey);
}

.repo-card__header,
.repo-card__info {
  padding: 0 var(--screen-padding);
}

.repo-card__title {
  font-size: var(--fnt-size-2xl);
  font-weight: 700;
  margin-bottom: 0.5rem;
}

.repo-card__info {
  display: flex;
  flex-direction: column;
  grid-row-gap: 1rem;
  row-gap: 1rem;
}

.repo-card__header {
  margin-bottom: 1rem;
  padding-bottom: 1rem;
  border-bottom: 1px solid var(--clr-extralight-grey);
}

.repo-card__link {
  font-weight: 600;
  color: var(--clr-primary);
  white-space: nowrap;
}

@media only screen and (min-width: 700px) {
  .repo-card {
    border: 3px solid var(--clr-extralight-grey);
    border-radius: 1.5rem;
    flex: 1 0 33%;
    min-width: 15rem;
  }

  .repo-card__title {
    margin-bottom: 0;
  }

  .repo-card__header {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    align-items: center;
    grid-column-gap: 1.5rem;
    -webkit-column-gap: 1.5rem;
            column-gap: 1.5rem;
  }

  .repo-card__info {
    display: grid;
    grid-template-columns: 1fr;
    grid-gap: 1rem;
    gap: 1rem;
  }
}

@media only screen and (min-width: 900px) {
  .repo-card {
    min-width: 30rem;
  }

  .repo-card__info {
    grid-template-columns: 1fr 1fr;
  }
}

.icon-message {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 3rem var(--screen-padding);
  font-size: var(--fnt-size-xl);
  font-weight: 600;
  grid-row-gap: 1rem;
  row-gap: 1rem;
  text-align: center;
}

.organizations {
  display: flex;
  flex-direction: column;
  padding-top: var(--screen-padding);
  padding-bottom: var(--screen-padding);
  grid-gap: 2rem;
  gap: 2rem;
}

.organizations__username {
  color: var(--clr-primary);
  font-weight: 700;
}

@media only screen and (min-width: 700px) {
  .organizations {
    flex-direction: row;
    flex-wrap: wrap;
    padding-left: 3rem;
    padding-right: 3rem;
  }
}

.org-card {
  padding: var(--screen-padding) 0;
  border-top: 3px solid var(--clr-extralight-grey);
  border-bottom: 3px solid var(--clr-extralight-grey);

  --avatar-size: 6rem;
}

.org-card__avatar {
  height: var(--avatar-size);
  width: var(--avatar-size);
  box-shadow: 0 0 0.5rem grey;
  border-radius: 1.5rem;
}

.org-card__header,
.org-card__description {
  padding: 0 var(--screen-padding);
}

.org-card__header {
  display: flex;
  align-items: center;
  grid-column-gap: 1rem;
  -webkit-column-gap: 1rem;
          column-gap: 1rem;
  margin-bottom: 1.5rem;
}

.org-card__info {
  display: flex;
  flex-direction: column;
  grid-row-gap: 0.5rem;
  row-gap: 0.5rem;
}

.org-card__name {
  color: var(--clr-primary);
  font-size: var(--fnt-size-2xl);
  font-weight: 700;
}

.org-card__id {
  font-size: var(--fnt-size-lg);
  font-weight: 600;
  white-space: nowrap;
}

.org-card__description {
  font-size: var(--fnt-size-lg);
  font-weight: 600;
  color: var(--clr-dark-grey);
}

@media only screen and (min-width: 700px) {
  .org-card {
    border: 3px solid var(--clr-extralight-grey);
    border-radius: 1.5rem;
    flex: 1 0 33%;
  }
}

.error-fallback__title {
  font-size: var(--fnt-size-2xl);
  font-weight: 700;
  color: var(--clr-primary);
}

.not-found {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: var(--screen-padding);
  margin-top: 5rem;
}

.not-found__title {
  font-size: 5rem;
  color: var(--clr-primary);
  margin-bottom: 0.5rem;
}

.not-found__subtitle {
  font-size: 2.25rem;
  text-align: center;
  margin-bottom: 0.5rem;
}

.not-found__text {
  font-size: var(--fnt-size-xl);
  max-width: 30rem;
  text-align: center;
  font-weight: 600;
  color: var(--clr-dark-grey);
}

.not-found__link {
  color: var(--clr-primary);
  font-weight: 700;
}

